export default {
  getProducts (payload = {}) {
    const page = payload.page !== null ? `?page=${payload.page}` : ''
    return axios.get(process.env.API_ENDPOINT + '/product' + page)
  },
  addProduct (payload = {}) {
    return axios.post(process.env.API_ENDPOINT + '/product', payload)
  },
  updateProduct (payload = {}) {
    payload._method = 'put'
    return axios.post(process.env.API_ENDPOINT + `/product/${payload.id}`, payload)
  },
  deleteProduct (payload = {}) {
    return axios.delete(process.env.API_ENDPOINT + `/product/${payload.id}`, payload)
  },
  getDailyPoints (payload) {
    const month = payload.month ? payload.month : null
    const year = payload.year ? payload.year : null
    const date = payload.date ? payload.date : null
    return axios.get(process.env.API_ENDPOINT + '/daily-points?month=' + month + '&date=' + date + '&year=' + year)
  }
}



